        return 'slide__questions-error'
      }
    },
    slideControl(command) {
      this.showControl = false;
      this.showResult = false;
      this.successAnswer = undefined;
      switch (command) {
        case 'prev':
          --this.slideIndex
          break
        case 'next':
          ++this.slideIndex;
          break
        case 'reload':
          this.slideIndex = 0;
          break
      }
    },
    setAnswer(answerVar) {
      this.showControl = true;
      this.successAnswer = answerVar === this.answerVar;
      this.showResult = true;
    },
    answerVarText() {
      let letter = this.questions[this.slideIndex].answerVar;
      switch (letter) {
        case 'a':
          return this.questions[this.slideIndex].a
        case 'b':
          return this.questions[this.slideIndex].b
        case 'c':
          return this.questions[this.slideIndex].c
        case 'd':
          return this.questions[this.slideIndex].d
      }
    }
  },
  watch: {
    slideIndex() {
      this.questionTitle = this.questions[this.slideIndex].questionTitle;
      this.questionText = this.questions[this.slideIndex].questionText;
      this.question = this.questions[this.slideIndex].question;
      this.answer = this.questions[this.slideIndex].answer;
      this.answerVar = this.questions[this.slideIndex].answerVar;
      this.slideInfoText = this.questions[this.slideIndex].slideInfoText;
      this.slideLink = this.questions[this.slideIndex].slideLink;
      this.a = this.questions[this.slideIndex].a;
      this.b = this.questions[this.slideIndex].b;
      this.c = this.questions[this.slideIndex].c;
      this.d = this.questions[this.slideIndex].d;
    }
  }
}
</script>
